<template>
  <el-card class="item_container">
    <a v-bind:href="item.url" class="item-wrapper">
      <img v-bind:src="item.imgUrl" class="item_img">
      <h2 class="item_title">{{ item.title }}</h2>
      <p class="item_extra">{{ item.extraText }}</p>
    </a>
  </el-card>
</template>
<script>
  export default{
    name:'list-item',
    props:{
      item: {
        title: String,
        url: String,
        imgUrl:{
          type: String,
          default: ''
        },
        extraText:{
          type: String,
          default: ''
        },
      }
    }
  }
</script>

<style lang="scss">
  @import "../assets/mixin";
  @import "../assets/config.scss";
  .item_container {
    @include wh(400px, 100px);
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
    .el-card__body {
      padding: 0;
      @include wh();
    }
    .item-wrapper {
      position: relative;
      @include wh();
      .item_img {
        @include wh(100px, 100px);
        @include positionTL();
        background-size: contain;
        background: center no-repeat;
      }
      .item_title {
        @include positionTL(50%, 110px);
        transform: translate(-50%, 0);
        color: $c_primary;

        font-size: $font20;
      }
      .item_extra {
        @include positionBR(10px, 10px);
        color: $c_primary;
        font-size: $font12;
      }
    }
  }
</style>
